.navigation {
  display: inline-flex;

  &__list {
    margin: 0;
    padding: 0 10px;
  }

  &__link {
    list-style-type: none;
    padding: 3px 0;

    .link {
      position: relative;
      color: #858686;

      &:after {
        content: '';
        position: absolute;
        top: 50%; right: -25px;
        width: 15px;
        height: 2px;
        transition: .25s;
      }

      &:hover {
        &:after {
          opacity: 1;
        }
      }
    }
  }
}
